.PictureList {
  display: grid;
  grid-gap: 1rem;
  max-width: 960px;
  margin: 0 auto;
  padding: 1rem;
  position: relative;
  opacity: 1;
  -webkit-transform: scale(1);
      -ms-transform: scale(1);
          transform: scale(1);
  -webkit-transition: all 200ms ease-in-out;
  -o-transition: all 200ms ease-in-out;
  transition: all 200ms ease-in-out;
}
.PictureList.portrait {
  grid-template: 300px 300px / 1fr 1fr 1fr;
}
.PictureList.landscape {
  grid-template: 200px 200px 200px / 1fr 1fr;
}
.PictureList.entering {
  opacity: 0.3;
  -webkit-transform: scale(1.1);
      -ms-transform: scale(1.1);
          transform: scale(1.1);
}
.PictureList.exiting {
  opacity: 0.3;
  -webkit-transform: scale(.75);
      -ms-transform: scale(.75);
          transform: scale(.75);
}



