.PictureItem {
  position: relative;
  overflow: hidden;
  margin: 0;
  padding: 0;
  -webkit-transition: 200ms ease;
  -o-transition: 200ms ease;
  transition: 200ms ease;
}
.PictureItem:hover {
  -webkit-box-shadow: 0 0 10px 2px var(--col-effect);
          box-shadow: 0 0 10px 2px var(--col-effect);
}
.PictureItem-image {
  overflow: hidden;
  -ms-flex: 1 0 50%;
      flex: 1 0 50%;
  width: 100%;
  height: 100%;
  -o-object-fit: cover;
     object-fit: cover;
}
.PictureItem-text {
  -ms-flex: 0 1 25%;
      flex: 0 1 25%;
  color: var(--col-font-item);
  background-color: var(--col-bg-item);
  -webkit-box-shadow: 0 0 .5rem .5rem var(--col-bg-item);
          box-shadow: 0 0 .5rem .5rem var(--col-bg-item);
}
.PictureItem-title {
  font-size: 16px;
}
.PictureItem-description {
  font-size: 12px;
}
.PictureItem.portrait {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
}
.PictureItem.landscape {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: row;
      flex-direction: row;
}



