.App {
  text-align: center;
  background-color: var(--col-bg-main);
  position: relative;
  min-height: 100vh;
}
.App-logo {
  height: 50px;
}
.App-header {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-direction: row;
      flex-direction: row;
  background-color: var(--col-bg-head);
  padding: 0.5rem 2rem;
  color: var(--col-font-alt);
  position: -webkit-sticky;
  position: sticky;
  top: 0;
  z-index: 100;
}
.App-title {
  font-size: 2rem;
  -ms-flex-positive: 1;
      flex-grow: 1;
  margin: 0;
}
.App-intro {
  font-size: large;
}
.App-button {
  border: none;
  background: none;
  padding: 0;
  -webkit-transition: 200ms ease;
  -o-transition: 200ms ease;
  transition: 200ms ease;
}
.App-button > img {
  display: block;
}
.App-button:hover {
  -webkit-transform: scale(1.2);
      -ms-transform: scale(1.2);
          transform: scale(1.2);
}



